/*----------------------  End Layout  ---------------------- */
/*----------------------  4 [Modules]  ---------------------- */
/**
    *
    * Module code style naming conventions
    *
    * .module--element__modifier { ... }
    *
    * sass written as:
    *
    * .module
    *    ...
    *   &--element
    *      ...
    *     &__modifier
    *        ...
    *
    * html syntax:
    *
    * <div class="module module--element__modifier"> ... </div>
    *
    * */
.data-box {
  display: flex;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 12;
  background: white;
  flex-direction: column;
  padding: 1rem 2.5rem 2.5rem;
}

